.cryptopay-lite-payment-details-table {
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin: 0 -1px 24px 0;
  text-align: left;
  width: 100%;
  border-collapse: separate;
  border-radius: 5px;
}

.cryptopay-lite-payment-details-table tr:not(:last-child) td,
.cryptopay-lite-payment-details-table tr:not(:last-child) th {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.cryptopay-lite-payment-details-table tr td,
.cryptopay-lite-payment-details-table tr th {
  padding: 10px;
}

.cryptopay-lite-payment-details-table tr th {
  padding-left: 10px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.cpl-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999999;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.5);
  display: none;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.cpl-modal .cpl-modal-content {
  position: relative;
  margin: 30px auto;
  width: 500px;
  border-radius: 5px;
  padding: 20px;
  padding-top: 45px;
}
.cpl-modal .cpl-modal-content .cpl-modal-close {
  position: fixed;
  top: 10px;
  right: 10px;
  font-size: 45px;
  cursor: pointer;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 520px) {
  .cpl-modal .cpl-modal-content {
    width: 320px;
  }
}
.cryptopay_lite-payment-gateway {
  width: 100%;
  display: flex;
  align-items: center;
}
.cryptopay_lite-payment-gateway .wc-block-components-payment-method-icons {
  flex: 1 0 auto;
  display: flex;
  justify-content: flex-end;
  padding-right: 16px;
}